.secondsection {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: auto;
  background: #fc9e9e;
}
.moartext {
  text-align: center;
}
.bidnowbtn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 250px;
  height: 70px;
  border: 1px solid black;
  border-radius: 40px;
}
.bidnowbtn h1 {
  font-size: 25px;
}
.bidnowbtn:hover {
  background: black;
  color: white;
}
.moarguest {
  margin-top: 20px;
  display: flex;
  flex-direction: row;
}
.buildingimg img {
  width: 400px;
}
.guestcontent img {
  width: 500px;
}
.guestcontent p {
  margin-top: 10px;
  width: 500px;
  font-size: 22px;
  font-weight: 300;
  line-height: 1.5;
}

.location {
  margin-top: 30px;
  display: flex;
  align-items: center;
}
.location img {
  width: 30px;
}
.location h1 {
  font-size: 32px;
}
.Artworks {
  display: flex;
  justify-content: center;
}
.Artworks img {
  max-width: 60%;
  height: auto;
  width: auto\9; /* ie8 */
}
